<template>
  <div class="login">
    <van-nav-bar class="logo-box">
      <template #left>
        <div class="logo"></div>
      </template>
      <template #right>
        <van-icon name="close" size="22" @click="gopage('Menu')" />
      </template>
    </van-nav-bar>
    <div class="box">
      <div class="box-title">欢迎回来！</div>
      <div class="box-content">Please login to your accounts</div>
    </div>
    <van-form @submit="login">
      <van-field v-model="userInfo.phone" name="手机号" placeholder="输入11位手机号" maxlength="11" />
      <van-field
        v-model="userInfo.password"
        :type="isEye ? 'text' : 'password'"
        name="密码"
        placeholder="字母数字_组合(6-16位)"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="loginPasswordRer"
        maxlength="16"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">登录</van-button>
      </div>
    </van-form>
    <van-button class="register" @click="showPopup">注册</van-button>
    <van-popup v-model="show" closeable position="bottom" :style="{ height: '58%' }">
      <div class="register-title">注册</div>
      <van-form @submit="register">
        <van-field
          v-model="reguserInfo.phone"
          name="手机号"
          placeholder="手机号 中国 (+86)"
          maxlength="11"
        />
        <van-field
          v-model="reguserInfo.nickName"
          name="昵称"
          placeholder="昵称 字母数字_组合(1-8)"
          maxlength="8"
        />
        <van-field
          v-model="reguserInfo.password"
          :type="isEye2 ? 'text' : 'password'"
          name="密码"
          placeholder="密码 以字母开头数字组合(6-16)"
          :right-icon="isEye2 ? 'eye-o':'closed-eye'"
          @click-right-icon="registerPassword"
          maxlength="16"
        />
        <div style="margin: 16px;">
          <van-button round block type="info" native-type="submit">注册</van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>

<script>
//导入样式
import "../assets/css/login.less";

//导入验证表单模块
import validForm from "../assets/js/validForm";

export default {
  data() {
    return {
      show: false,
      //用户注册信息
      userInfo: {
        phone: "",
        password: "",
      },
      reguserInfo: {
        nickName: "",
        phone: "",
        password: "",
      },
      //false: 闭合，true：睁开
      isEye: false,
      isEye2: false,
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },

    //切换密码的格式
    loginPasswordRer() {
      this.isEye = !this.isEye;
    },
     //切换密码格式
    registerPassword(){
        this.isEye2 = !this.isEye2;
    },
    //跳转路径
    gopage(name){
      this.$router.push({name:name});
    },
    //登录
    login() {
      //进行验证表单
      if (validForm.validUserForm(this.userInfo)) {
        //复制对象
        let data = Object.assign({}, this.userInfo);

        //请求密钥
        data.appkey = this.appkey;

        //开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,

          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0,
        });

        //发起登录请求
        this.axios({
          method: "POST",
          url: "/login",

          //post传递参数需要写在data
          data,
        }).then((result) => {
            //关闭加载提示
            this.$toast.clear();

            if (result.data.code == 200) {
              //跳转到菜单

              //存储token，用于后面登录验证
              localStorage.setItem("__Tk", result.data.token);

              this.$router.push({ name: "Menu" });
            } else {
              this.$toast(result.data.msg);
            }
          }).catch((err) => {
            //关闭加载提示
            this.$toast.clear();
          });
      }
    },
    //注册
    register() {
      //进行表单验证
      if (validForm.validUserForm(this.reguserInfo)) {
        //复制对象
        let data = Object.assign({}, this.reguserInfo);
        // 请求秘钥
        data.appkey = this.appkey;

        // 开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,

          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0,
        });

        //发起注册请求
        this.axios({
          method: "POST",
          url: "/register",

          // post传递参数需要写在data
          data,
        })
          .then((result) => {
            if (result.data.code == 100) {
              //跳转到登录页面
              // this.$router.push({ name: "Login" });
            this.$toast.clear();

              this.show = false;
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch((err) => {
            //关闭加载提示
            this.$toast.clear();
          });
      }
    },
  },
};
</script>

<style lang="less" scoped>
</style>